import React, { Component } from "react";

import {
  Flex,
  List,
  InputItem,
  Picker,
  ImagePicker,
  TextareaItem,
  Modal,
  NavBar,
  Icon,
  Toast,
} from "antd-mobile";

import HousePackage from "../../../components/HousePackage";

import styles from "./index.module.css";
import { uploadImgs } from "../../../utils/api/House";
import { pubHouse } from "../../../utils/api/user";

const alert = Modal.alert;

// 
const roomTypeData = [
  { label: "", value: "ROOM|d4a692e4-a177-37fd" },
  { label: "", value: "ROOM|d1a00384-5801-d5cd" },
  { label: "", value: "ROOM|20903ae0-c7bc-f2e2" },
  { label: "", value: "ROOM|ce2a5daa-811d-2f49" },
  { label: "+", value: "ROOM|2731c38c-5b19-ff7f" },
];

// 
const orientedData = [
  { label: "", value: "ORIEN|141b98bf-1ad0-11e3" },
  { label: "", value: "ORIEN|103fb3aa-e8b4-de0e" },
  { label: "", value: "ORIEN|61e99445-e95e-7f37" },
  { label: "", value: "ORIEN|caa6f80b-b764-c2df" },
  { label: "", value: "ORIEN|dfb1b36b-e0d1-0977" },
  { label: "", value: "ORIEN|67ac2205-7e0f-c057" },
  { label: "", value: "ORIEN|2354e89e-3918-9cef" },
  { label: "", value: "ORIEN|80795f1a-e32f-feb9" },
];

// 
const floorData = [
  { label: "", value: "FLOOR|1" },
  { label: "", value: "FLOOR|2" },
  { label: "", value: "FLOOR|3" },
];

export default class RentAdd extends Component {
  constructor(props) {
    super(props);
    // 
    const { data = {} } = this.props.location;
    let community = {
      id: "",
      name: "",
    };
    // if (data) {
    community = {
      id: data.id,
      name: data.name,
      // };
    };
    this.state = {
      // 
      tempSlides: [],

      // id
      community: community,
      // 
      price: "",
      // 
      size: "",
      // 
      roomType: "",
      // 
      floor: "",
      // 
      oriented: "",
      // 
      title: "",
      // 
      houseImg: "",
      // 
      supporting: "",
      // 
      description: "",
    };
  }

  // 
  onCancel = () => {
    alert("", "?", [
      {
        text: "",
        onPress: async () => this.props.history.go(-1),
      },
      {
        text: "",
      },
    ]);
  };

  // 
  handlerInput = (val, name) => {
    this.setState({
      [name]: val,
    });
  };

  // 
  addHouse = async () => {
    // 
    // 
    const {
      tempSlides,
      title,
      description,
      oriented,
      price,
      roomType,
      size,
      floor,
      community,
    } = this.state;
    // 
    let supporting = this.supporting;
    // 
    if (!title || !size || !price || tempSlides.length === 0) {
      return Toast.fail("");
    }
    // 
    let serverImgs;
    if (tempSlides.length) {
      // =
      // 
      let fd = new FormData();
      tempSlides.forEach((item) => fd.append("file", item.file));
      let { status, data } = await uploadImgs(fd);
      if (status === 200) {
        serverImgs = data.join("|");
      }

      // 
      let postData = {
        community: community.id,
        price,
        size,
        roomType,
        floor,
        oriented,
        description,
        houseImg: serverImgs,
        supporting,
        title,
      };
      // 
      let res = await pubHouse(postData);
      if (res.status === 200) {
        Toast.success("", 1, () => {
          // 
          this.props.history.push("/rent");
        });
      } else {
        // token
        Toast.fail("");
      }
    }
  };

  render() {
    const Item = List.Item;
    const { history } = this.props;
    const {
      community,
      price,
      size,
      roomType,
      floor,
      oriented,
      description,
      tempSlides,
      title,
    } = this.state;

    return (
      <div className={styles.root}>
        <NavBar
          className={styles.navHeader}
          mode="dark"
          icon={<Icon type="left" />}
          onLeftClick={this.onCancel}
        >
          
        </NavBar>
        <List
          className={styles.header}
          renderHeader={() => ""}
          data-role="rent-list"
        >
          {/*  */}
          <Item
            extra={community.name || ""}
            arrow="horizontal"
            onClick={() => history.replace("/rent/search")}
          >
            
          </Item>
          <InputItem
            placeholder="/"
            extra="/"
            type="number"
            onChange={(v) => {
              this.handlerInput(v, "price");
            }}
            value={price}
          >
            &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;
          </InputItem>
          <InputItem
            placeholder=""
            extra=""
            type="number"
            onChange={(v) => {
              this.handlerInput(v, "size");
            }}
            value={size}
          >
            
          </InputItem>
          <Picker
            data={roomTypeData}
            onChange={(v) => {
              this.handlerInput(v[0], "roomType");
            }}
            value={[roomType]}
            cols={1}
          >
            <Item arrow="horizontal">
              &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;
            </Item>
          </Picker>

          <Picker
            data={floorData}
            onChange={(v) => {
              this.handlerInput(v[0], "floor");
            }}
            value={[floor]}
            cols={1}
          >
            <Item arrow="horizontal"></Item>
          </Picker>
          <Picker
            data={orientedData}
            onChange={(v) => {
              this.handlerInput(v[0], "oriented");
            }}
            value={[oriented]}
            cols={1}
          >
            <Item arrow="horizontal">
              &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;
            </Item>
          </Picker>
        </List>

        <List
          className={styles.title}
          renderHeader={() => ""}
          data-role="rent-list"
        >
          <InputItem
            placeholder="  2 5000"
            onChange={(v) => {
              this.handlerInput(v, "title");
            }}
            value={title}
          />
        </List>

        <List
          className={styles.pics}
          renderHeader={() => ""}
          data-role="rent-list"
        >
          <ImagePicker
            files={tempSlides}
            multiple={true}
            onChange={(files) => {
              this.setState({
                tempSlides: files,
              });
            }}
            className={styles.imgpicker}
          />
        </List>

        <List
          className={styles.supporting}
          renderHeader={() => ""}
          data-role="rent-list"
        >
          <HousePackage
            select
            onSelect={(val) => {
              // console.log(val);
              this.supporting = val.join("|");
              // console.log(this.supporting);
            }}
          />
        </List>

        <List
          className={styles.desc}
          renderHeader={() => ""}
          data-role="rent-list"
        >
          <TextareaItem
            rows={5}
            placeholder=""
            autoHeight
            onChange={(v) => {
              this.handlerInput(v, "description");
            }}
            value={description}
          />
        </List>

        <Flex className={styles.bottom}>
          <Flex.Item className={styles.cancel} onClick={this.onCancel}>
            
          </Flex.Item>
          <Flex.Item className={styles.confirm} onClick={this.addHouse}>
            
          </Flex.Item>
        </Flex>
      </div>
    );
  }
}
